const app = getApp();
import helper from '../common/helper.js';
import parser from '@/components/jyf-parser';
export default {
	data() {
		return {
			userInfo: {},
			focus: false,
			isShow: '0',
			isHide: '0',
			info: {},
			nodes: [],
			commentList: [],
			articleDetail: null,
			options: null,
			imgUrl: '',
			page: '1',
			postContent: '',
			just_landlord: '',
			comment_id: '',
			isSex: '0',
			type: '',
			token: ''
		};
	},
	components: {
		'jyf-parser': parser
	},
	onLoad(options) {
		this.options = options;
		this.imgUrl = helper.imgUrl;
		this.token = uni.getStorageSync('token')
	},
	onShow() {
		// 文章详情加载
		this.getArticleDetail();
		//评论列表
		this.getComment();
	},
	onShareAppMessage() {
		let url = this.getPageUrl();
		return {
			title: this.articleDetail.title,
			path: url
		};
	},
	methods: {
		// 下载doc
		linkUrl(extra) {
			console.log(extra);


























			uni.setClipboardData({
			    data: extra,
			    success: function () {
			        uni.showToast({
			        	title: '复制成功,粘贴到浏览器下载',
			        	icon: 'none'
			        })
			    }
			});

			// uni.downloadFile({
			//     url: extra, //仅为示例，并非真实的资源
			//     success: (res) => {
			//         if (res.statusCode === 200) {
			// 					console.log(res)
			//           uni.saveFile({
			//             tempFilePath: res.tempFilePath,
			//             success: function (res) {
			// 							console.log(res)
			//               var savedFilePath = res.savedFilePath;
			//             }
			//           });
			//         }
			//     }
			// });
			// app.globalData.link = extra
			// let name = '下载'
			// uni.navigateTo({
			// 	url:`/pages/iframe?name=${name}`
			// })
		},
		goVIPPage(e) {
			// 判断当前用户是否为普通用户
			if (this.articleDetail.user.type == 'normal') {
				let content_type = e;
				// 判断当前点击文本是否为会员可看
				if (content_type == 'member') {
					uni.showModal({
						title: '提示',
						content: '是否前往开通会员',
						success: res => {
							if (res.confirm) {
								// console.log('用户点击确定');
								// 进入开通会员页
								this.goVip();
							} else if (res.cancel) {
								// console.log('用户点击取消');
							}
						}
					});
				}
			}
		},
		goVip() {
			uni.navigateTo({
				url: '/pages/meVIP'
			});
		},
		//获取发布内容
		getContent(e) {
			this.postContent = e.detail.value;
			// console.log(e);
		},
		// 文章详情加载
		getArticleDetail() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}/posts/show`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.options.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					// console.log(res);
					if (res.data.status_code == 200) {
						this.articleDetail = res.data;
						console.log('----------------')
						console.log(this.articleDetail.extras)
						if (this.articleDetail.user.sex == 'f') {
							this.isSex = '1';
						}
					}else if(res.data.status_code == 202){
						this.articleDetail = null
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						})
						setTimeout(e => {
							uni.redirectTo({
								url:"./meVIP"
							})
						}, 2000);
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},

		shareFriend() {
			//分享到微信朋友
			this.share('WXSceneSession');
		},
		shareFriendcricle() {
			//分享到微信朋友圈
			this.share('WXSenceTimeline');
		},
		// 获取当前页路径及参数,用于分享
		getPageUrl() {
			// pages/articleDetail?id=5&name=222&aaa=2344asfdasdf
			// let options = {id: '5', name: '222', aaa: '2344asfdasdf'}
			let pageNode = getCurrentPages();
			pageNode = pageNode[pageNode.length - 1];
			let url = pageNode.route;
			let options = pageNode.options;
			let optionsString = '?';
			for (let key in options) {
				optionsString += key;
				optionsString += '=';
				optionsString += options[key];
				optionsString += '&';
			}
			optionsString = optionsString.substring(0, optionsString.length - 1);
			url += optionsString;
			return url;
		},
		share(WXSenceType) {
			// 获取页面路径
			let url = this.getPageUrl();
			uni.share({
				provider: 'weixin',
				scene: WXSenceType,
				type: 0,
				href: url,
				title: this.articleDetail.title,
				summary: '',
				imageUrl: '',
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		},
		//打赏
		postReward() {
			uni.showLoading({
				title: '打赏中...'
			});
			uni.request({
				url: `${helper.requestUrl}/posts/reward`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: '打赏成功'
						});
						this.articleDetail.rewards_count += 1;
					} else {
						uni.showToast({
							title: '打赏失败',
							icon: 'none'
						});
					}
				}
			});
		},
		// 文章--添加--收藏
		addCollection() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/add_collection`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '1') {
						// 修改帖子的收藏状态
						this.articleDetail.is_collections == 1;
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 文章--取消--收藏
		delCollection() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/del_collection`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '1') {
						// 修改帖子的收藏状态
						this.articleDetail.is_collections == 0;
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		addFollow(type) {
			// 关注用户
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/add_follow`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					follow_id: this.articleDetail.user_id,
					type: type
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message
						});
						this.articleDetail.is_follow = 1;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 评论列表
		getComment() {
			uni.request({
				url: `${helper.requestUrl}/posts/post-comments`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.options.id,
					just_landlord: this.just_landlord,
					page: this.page,
					page_size: '10'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res, '**********');
					if (res.data.status_code == '200') {
						this.commentList = this.commentList.concat(res.data.data);
					} else {
					}
				}
			});
		},
		//发布评论
		postDiscuss() {
			if (this.postContent == '') {
				uni.showToast({
					title: '请输入发表内容',
					icon: 'none'
				});
			} else {
				if (this.isShow == '0') {
					this.getPost();
				} else if (this.isShow == '1') {
					this.postReply();
				}
			}
		},
		// 评论
		getPost() {
			uni.showLoading({
				title: '评论提交中...'
			});
			uni.request({
				url: `${helper.requestUrl}/posts/send-comment`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.options.id,
					content: this.postContent
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
						this.postContent = ' ';
					} else {
						uni.showToast({
							title: '发表失败',
							icon: 'none'
						});
					}
				}
			});
		},
		// 回复评论
		reply(e, i) {
			console.log(e, i);
			this.comment_id = e;
			this.isShow = i;
			this.focus = true;
		},
		postReply() {
			uni.showLoading({
				title: '评论发布中...'
			});
			uni.request({
				url: `${helper.requestUrl}/posts/send-reply`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					comment_id: this.comment_id,
					content: this.postContent
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
						this.postContent = ' ';
						this.isShow = '0';
					} else {
						uni.showToast({
							title: '发表失败',
							icon: 'none'
						});
					}
				}
			});
		},
		//只看楼主
		commentLandlord(e) {
			console.log(e);
			this.just_landlord = e;
			this.commentList = [];
			this.page = '1';
			this.getComment();
		},
		// 点赞
		clickZan(e) {
			this.isHide = '1';
			uni.showLoading({
				title: '点赞中...'
			});
			uni.request({
				url: `${helper.requestUrl}/posts/like`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: '点赞成功',
							icon: 'none'
						});
						this.articleDetail.like += 1;
						this.articleDetail.is_collections = 1;
					} else {
						uni.showToast({
							title: '点赞失败',
							icon: 'none'
						});
					}
				}
			});
		},
		onReachBottom() {
			this.page++;
			this.getComment();
		}
	}
};
